.landing-page {

    height: 100vh;
    width: 85%;
    @include center-div;
    justify-content: center;
    float: right;
    background: $color2;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #FFFFFF, $color2);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #FFFFFF, $color2); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    text-align: center;


    &__name-title {
        font-size: 10rem;
        color: $color1;
        font-weight: bold;
    }

    &__job-title {
        font-size: 3rem;
        margin-top: 25px;
        color: $color1;
        font-weight: bold;
    }

    &__scroll-to-top-btn {
    height: 90vh;
    position: fixed;
    left: 90%;
    margin-top: 80px;
    }
}

/* MEDIA QUERIES */

/* IPAD */
@media screen and (max-width: 768px) {
    .landing-page {
        &__name-title {
            font-size: 7rem;
        }

        &__job-title {
            font-size: 2rem;
        }
    }
}
/* PHONE */
@media screen and (max-width: 627px) {
    .landing-page {
        width: 100%;
        overflow: hidden;
        &__name-title {
            font-size: 5rem;
        }
        &__job-title {
            font-size: 1.5rem;
        }
    }
}