.cv-page {

    height: 100vh;
    width: 85%;
    @include center-div;
    @include scroll-snap-align-start;
    float: right;

    background-color: $color2;

    &__cv-title {
        font-size: 5rem;
        margin-top: 100px;
        color: $color1;
    }

    &__container {
        width: 100%;
        height: 100vh;
        display: flex;
        justify-content: center;
        align-items: center;

        &__download-box {
        height: 400px;
        border-radius: 50px;
        display: flex;
        align-items: center;
        flex-direction: column;

        &__download-link {
            transition: all .5s;
            text-decoration: none;
            color: $color1;

            &:hover {
                color: $color4;
                transform: translateY(-4px);
                
            }

            &__text {
                padding-bottom: 40px;
                font-size: 3rem;
                color: $color1;
                text-align: center;
                font-weight: bold;
                white-space: pre-wrap;
            }
        }
        }
    }
}

/* MEDIA QUERIES */

/* IPAD */
@media screen and (max-width: 768px) {
    .cv-page {
        &__container {
            &__download-box {
                height: auto;
                margin-top: -50px;
    
            &__download-link {
                &__text {
                    font-size: 2.5rem;
                }
            }
            }
        }
    }
}

/* PHONE */
@media screen and (max-width: 627px) {
    .cv-page {
        width: 100%;

        &__container {
            &__download-box {    
            &__download-link {
                &__text {
                    font-size: 1.5rem;
                }
            }
            }
        }
}
}
